import i18next from './translator';

let genres;
if (i18next.language === 'en') {
  genres = [
    { id: 28, name: 'Action' },
    { id: 12, name: 'Adventure' },
    { id: 16, name: 'Animation' },
    { id: 35, name: 'Comedy' },
    { id: 80, name: 'Crime' },
    { id: 99, name: 'Documentary' },
    { id: 18, name: 'Drama' },
    { id: 10751, name: 'Family' },
    { id: 14, name: 'Fantasy' },
    { id: 36, name: 'History' },
    { id: 27, name: 'Horror' },
    { id: 10402, name: 'Music' },
    { id: 9648, name: 'Mystery' },
    { id: 10749, name: 'Romance' },
    { id: 878, name: 'Science Fiction' },
    { id: 10770, name: 'TV Movie' },
    { id: 53, name: 'Thriller' },
    { id: 10752, name: 'War' },
    { id: 37, name: 'Western' },
  ];
} else if (i18next.language === 'uk') {
  genres = [
    { id: 28, name: '' },
    { id: 12, name: '' },
    { id: 16, name: '' },
    { id: 35, name: '' },
    { id: 80, name: '' },
    { id: 99, name: '' },
    { id: 18, name: '' },
    { id: 10751, name: '' },
    { id: 14, name: '' },
    { id: 36, name: '' },
    { id: 27, name: '' },
    { id: 10402, name: '' },
    { id: 9648, name: '' },
    { id: 10749, name: '' },
    { id: 878, name: '' },
    { id: 10770, name: ' ' },
    { id: 53, name: '' },
    { id: 10752, name: '' },
    { id: 37, name: '' },
  ];
}

const getGenreById = id => {
  const genre = genres.find(genre => genre.id === id);
  return genre ? genre.name : '';
};

const getGenresById = genresArray => {
  if (genresArray.length > 0) {
    const allGenres = [];
    genresArray.forEach(id => {
      const genreName = getGenreById(id);
      if (!genreName) {
        return;
      }
      allGenres.push(genreName);
    });
    return allGenres.join(', ');
  } else {
    return '';
  }
};

export { getGenresById };
